import React, { useEffect, useState } from "react";
import { useLocation } from "react-router";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/components/prism-bash";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post27() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "postgresql", "wal-g"];

  const configurePrismTheme = (themeMode) => {
    let link = document.getElementById("prism-theme");

    if (!link) {
      link = document.createElement("link");
      link.rel = "stylesheet";
      link.id = "prism-theme";
      document.head.appendChild(link);
    }

    link.href =
      themeMode === "dark"
        ? "https://unpkg.com/prismjs/themes/prism-twilight.css"
        : "https://unpkg.com/prismjs/themes/prism.css";
  };

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
      configurePrismTheme("dark");
    } else {
      setThemeMode("light");
      configurePrismTheme("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
      configurePrismTheme(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Azure PostgreSQL Backup with WAL-G</title>
        <meta name="description" content="Azure PostgreSQL Backup with WAL-G" />
        <meta name="keywords" content="azure, postgresql, wal-g" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta
          property="og:title"
          content="Azure PostgreSQL Backup with WAL-G"
        />
        <meta
          property="og:description"
          content="Azure PostgreSQL Backup with WAL-G"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Azure PostgreSQL Backup with WAL-G
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Aug 23, 2025
                </time>
              </span>
              <div className="line-break"></div>
              <div
                className={
                  themeMode === "dark"
                    ? "dark articleBody"
                    : "light articleBody"
                }
              >
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Several tools are available for performing physical backups of
                  PostgreSQL databases. PostgreSQL includes the pg_basebackup
                  utility, which copies the entire data directory at the file
                  system level and can optionally include Write-Ahead Log (WAL)
                  files to support point-in-time recovery. In this article I
                  will focus on the archival restoration tool WAL-G.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  WAL-G supports multiple compression algorithms, utilizes
                  multiple processors for performance, allows non-exclusive base
                  backups in PostgreSQL, and stores database backups in various
                  cloud storage services [1]. The default compression method in
                  WAL-G is LZ4, which provides very fast speed, but a moderate
                  compression ratio. WAL-G supports several advanced compression
                  algorithms to optimize the backup size and performance,
                  including LZMA, ZSTD, and Brotli.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I created a project with a backup solution for Azure Database
                  for PostgreSQL Flexible Server using WAL-G. The GitHub
                  repository of the project is the following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/azure-postgresql-backup-wal-g"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/azure-postgresql-backup-wal-g
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The WAL-G binary is installed in one Azure Virtual Machine at
                  startup time using the custom data feature [2]. Azure Database
                  for PostgreSQL Flexible Server does not support physical
                  backups, so in order to enable WAL-G to take backups of the
                  database, a PostgreSQL server is installed in the Azure
                  Virtual Machine, and the data is replicated from the Azure
                  Database for PostgreSQL Flexible Server instance.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Azure Database for PostgreSQL Flexible Server supports logical
                  replication using PostgreSQL's native replication capabilities
                  [3]. PostgreSQL's native logical replication does not
                  replicate schema changes (DDL), so any such changes must be
                  applied manually on the target database.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following server parameters must be configured in the
                  Azure Database for PostgreSQL Flexible Server:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-none">
                    wal_level = logical
                    <br />
                    max_replication_slots = 4
                    <br />
                    max_wal_senders = 5
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The default value for the parameter{" "}
                  <i>max_replication_slots</i> is 10. The default value for the
                  parameter <i>max_wal_senders</i> is: 2 (if HA is enabled) +
                  number of read replicas provisioned +
                  slots_used_in_logical_replication.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Restart the Azure Database for PostgreSQL Flexible Server
                  instance for the configuration changes to be applied.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Create a replicator user in the PostgreSQL Flexible Server
                  instance:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-sql">
                    CREATE ROLE replicator WITH REPLICATION LOGIN PASSWORD
                    'replicator_password';
                    <br />
                    GRANT CONNECT ON DATABASE database_name TO replicator;
                    <br />
                    GRANT azure_pg_admin TO replicator;
                    <br />
                    ALTER ROLE replicator REPLICATION LOGIN;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Create a publication for the database in Azure Database for
                  PostgreSQL Flexible Server:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-sql">
                    CREATE PUBLICATION dbpub FOR ALL TABLES;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Create a Subscription in PostgreSQL server installed in the
                  Azure Virtual Machine:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-sql">
                    CREATE SUBSCRIPTION dbsub CONNECTION
                    'host=azure_database_host port=5432 dbname=database_name
                    user=replicator password=replicator_password
                    sslmode=require' PUBLICATION dbpub;
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The PostgreSQL database is configured to accept remote
                  connections and replication in pg_hba.conf:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-none">
                    host all all 0.0.0.0/0 md5
                    <br />
                    listen_addresses = '*'
                    <br />
                    host replication all 0.0.0.0/0 md5
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The PostgreSQL database is additionally configured with the
                  following server parameters in postgresql.conf:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-none">
                    wal_level = logical
                    <br />
                    max_replication_slots = 4
                    <br />
                    max_wal_senders = 5
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Once the database replication is working correctly, the next
                  step is to configure WAL-G.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  WAL-G can be configured either via environment variables or
                  environment files located in <i>/etc/wal-g.d/env</i>. WAL-G
                  makes use of the PostgreSQL environment variables PGHOST,
                  PGPORT, PGUSER, PGPASSWORD, PGDATABASE to connect to the
                  PostgreSQL database. In addition the environment variable
                  WALG_AZ_PREFIX must be set to <i>azure://backups/walg</i> in
                  order to instruct WAL-G to use an Azure Storage Account for
                  storing backups. The environment variable
                  AZURE_STORAGE_ACCOUNT must contain the name of the Storage
                  Account.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  WAL-G requires the following parameters in postgresql.conf to
                  enable the archiving of the backups:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-none">
                    archive_mode = on
                    <br />
                    archive_command = 'wal-g wal-push %p'
                    <br />
                    archive_timeout = 60
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The parameter <i>archive_mode</i> controls whether WAL
                  (Write-Ahead Log) segment archiving is enabled, which is
                  essential for features such as point-in-time recovery,
                  continuous backups, and WAL shipping.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  With the above configuration, WAL-G automatically archives WAL
                  files as they are generated by PostgreSQL, integrating with
                  the PostgreSQL's archive_command to push each WAL file to the
                  remote storage immediately after a WAL segment is complete.
                  The parameter <i>archive_timeout</i> instructs PostgreSQL to
                  switch WAL segments more regularly (e.g. 60 seconds).
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The following command must be executed to perform the first
                  base backup with WAL-G:
                </p>
                <pre className="mt-4 text-left h-68 w-full">
                  <code className="language-bash">
                    export PGDATA=/var/lib/postgresql/16/main
                    <br />
                    sudo -u postgres envdir /etc/wal-g.d/env wal-g backup-push
                    $PGDATA
                  </code>
                </pre>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The physical backups and WAL archives are stored in the Azure
                  Storage Account container as blobs. A chron job can
                  additionally be configured in the Azure Virtual Machine to
                  periodically execute the physical backups of the PostgreSQL
                  database.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Executing regular physical backups leads to faster database
                  recovery because the recovery procedure needs to replay fewer
                  WAL archive files to reach the desired state [4]. During a
                  point-in-time restore, WAL-G downloads and restores the
                  closest base backup of the database, then replays the WAL
                  files (incremental changes) to bring the database to the
                  desired recovery point.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://github.com/wal-g/wal-g"
                    target="_blank"
                    rel="noreferrer"
                  >
                    wal-g
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://learn.microsoft.com/en-us/azure/virtual-machines/custom-data"
                    target="_blank"
                    rel="noreferrer"
                  >
                    azure/virtual-machines/custom-data
                  </a>
                  <br />
                  [3]{" "}
                  <a
                    className="underline"
                    href="https://learn.microsoft.com/en-us/azure/postgresql/flexible-server/concepts-logical"
                    target="_blank"
                    rel="noreferrer"
                  >
                    azure/postgresql/flexible-server/concepts-logical
                  </a>
                  <br />
                  [4]{" "}
                  <a
                    className="underline"
                    href="https://www.postgresql.org/docs/current/continuous-archiving.html"
                    target="_blank"
                    rel="noreferrer"
                  >
                    postgresql/continuous-archiving
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post27;
