import React, { useEffect, useState } from "react";
import { useLocation } from "react-router";
import { Helmet } from "react-helmet";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/components/prism-bash";
import "prismjs/components/prism-cobol";
import "prismjs/components/prism-python";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post26() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "postgresql"];

  const configurePrismTheme = (themeMode) => {
    let link = document.getElementById("prism-theme");

    if (!link) {
      link = document.createElement("link");
      link.rel = "stylesheet";
      link.id = "prism-theme";
      document.head.appendChild(link);
    }

    link.href =
      themeMode === "dark"
        ? "https://unpkg.com/prismjs/themes/prism-twilight.css"
        : "https://unpkg.com/prismjs/themes/prism.css";
  };

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
      configurePrismTheme("dark");
    } else {
      setThemeMode("light");
      configurePrismTheme("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
      configurePrismTheme(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <>
      <Helmet>
        <title>Azure PostgreSQL Backup Automation</title>
        <meta name="description" content="Azure PostgreSQL Backup Automation" />
        <meta name="keywords" content="azure, postgresql" />
        <meta name="author" content="Marco Cella" />
        {/* Open Graph tags for social media */}
        <meta
          property="og:title"
          content="Azure PostgreSQL Backup Automation"
        />
        <meta
          property="og:description"
          content="Azure PostgreSQL Backup Automation"
        />
        <meta property="og:type" content="website" />
      </Helmet>

      <div className={themeMode === "dark" ? "dark" : "light"}>
        <Navigation
          themeMode={themeMode}
          onThemeChange={toggleThemeMode}
        ></Navigation>
        <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
          <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
            <div className="mt-16 mb-4 flex justify-items-start content">
              <h1 className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
                Azure PostgreSQL Backup Automation
              </h1>
              <div className="line-break"></div>
              <span className="flex items-center">
                <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
                <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                  Jul 26, 2025
                </time>
              </span>
              <div className="line-break"></div>
              <div
                className={
                  themeMode === "dark"
                    ? "dark articleBody"
                    : "light articleBody"
                }
              >
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  If you are using Azure Database for PostgreSQL Flexible
                  Server, Azure offers the Azure Backup Vault service to backup
                  the database resources. It supports long-term retention, along
                  with advanced features such as immutability and soft-delete to
                  enhance data protection and recovery.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  Microsoft has recently introduced restrictions to the backup
                  solution of Azure Database for PostgreSQL Flexible Server,
                  limiting it to support only weekly backups at this time [1].
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  To address the limitations of Azure Backup, some alternative
                  tools and solutions are available. In this article, I will
                  focus on the pg_dump utility provided by PostgreSQL, which is
                  used to create full backups of a PostgreSQL database [2]. SQL
                  Dump performs logical backups by generating a file containing
                  SQL commands that can recreate the database in the same state
                  it was in at the time of the dump.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The pg_dump utility is a PostgreSQL client application that
                  cannot be executed directly on the Azure Database for
                  PostgreSQL Flexible Server. However, it can run from a remote
                  host to create backups of the database hosted on the
                  PostgreSQL Flexible Server.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  It's important to consider the limitations of the tools when
                  designing a backup solution, most notably, SQL Dump does not
                  support point-in-time recovery (PITR). The pg_dump utility
                  performs logical backups, generating SQL statements that
                  represent the database's schema and data. In contrast,
                  physical backups, such as those created with the pg_basebackup
                  utility, copy the entire data directory at the file system
                  level and can optionally include Write-Ahead Log (WAL) files
                  to support point-in-time recovery.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  I created a project with a backup automation solution for
                  Azure Database for PostgreSQL Flexible Server using the
                  pg_dump utility. The GitHub repository of the project is the
                  following:
                  <br />
                  <a
                    className="underline"
                    href="https://github.com/sitMCella/azure-postgresql-backup"
                    target="_blank"
                    rel="noreferrer"
                  >
                    sitMCella/azure-postgresql-backup
                  </a>
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The automation solution consists of a CronJob deployed in one
                  Azure Kubernetes Service (AKS) cluster. The CronJob includes
                  two containers: the first container performs a full backup of
                  a PostgreSQL database hosted on the Azure Database for
                  PostgreSQL Flexible Server resource, while the second
                  container uploads the resulting dump file as a blob to an
                  Azure Storage Account.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The CronJob resource is configured to run on a daily schedule,
                  but this can be adjusted to meet specific requirements.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The AKS cluster is configured with Workload Identity to allow
                  Azure CLI to authenticate with a Managed Identity when
                  uploading dump files to the Azure Storage Account. The pg_dump
                  command, by contrast, uses a PostgreSQL username and password
                  for authentication. The specified user role must have read
                  access on the database objects in order to perform the backup
                  (CONNECT permission on the database, USAGE permission on the
                  schemas, and SELECT permission on tables and views).
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  The pg_dump utility provided by PostgreSQL is a simple and
                  effective tool for creating full database backups. However, it
                  may not be practical for frequent backups or for backing up
                  large databases due to its time and resource requirements.
                </p>
                <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                  [1]{" "}
                  <a
                    className="underline"
                    href="https://learn.microsoft.com/en-us/azure/backup/backup-azure-database-postgresql-flex-support-matrix#limitation"
                    target="_blank"
                    rel="noreferrer"
                  >
                    backup-azure-database-postgresql-flex
                  </a>
                  <br />
                  [2]{" "}
                  <a
                    className="underline"
                    href="https://www.postgresql.org/docs/16/backup-dump.html"
                    target="_blank"
                    rel="noreferrer"
                  >
                    backup-dump
                  </a>
                </p>
              </div>
            </div>
            <div className="mt-8 flex items-center text-left">
              <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
              {tags.map((item, index) => (
                <span
                  key={`tag-${index}`}
                  className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
                >
                  {item}
                </span>
              ))}
            </div>
          </div>
          <div className="mb-16"></div>
          <div></div>
        </div>
        <Footer></Footer>
      </div>
    </>
  );
}

export default Post26;
